/**
 * External dependencies
 */
import { createRoot } from '@wordpress/element';
import MyTabs from './TabComponent';
import { AppProvider } from './contexts/FormContext';
import ErrorBoundery from "./components/ErrorBoundery";
/**
 * Internal dependencies
 */

//const TabList = Tabs.TabList;

document.addEventListener('DOMContentLoaded',()=>{

	const buttonWp = document.querySelector('#mainform > p.submit > button');
		  buttonWp.style = 'display:none';

	const settingsContainer = document.getElementById(
		'react-app'
	);

	const root = createRoot(settingsContainer);

	if ( settingsContainer ) {
		root.render(
			<ErrorBoundery>
				<AppProvider>
					<MyTabs/>
				</AppProvider>
			</ErrorBoundery>
			);
	//	render( <MyUncontrolledTabs />, settingsContainer );
	}
	
})


