import {useState} from 'react';

import {
	Divider,
	Flex,
	Form,
	Radio,
	Space
} from "antd";


const GroupsCargo = () => {
	
	const options = [
		{label: "   -  ", value: "SINGLE_ITEM_SINGLE_SPACE"},
		{label: "   ,   ", value: "SEPARATED_CARGO_SPACE"},
		{label: "    1 ", value: "ONE_CARGO_SPACE"}
	];
	
	const [value, setValue] = useState(dellinVars.options.groupType);
	
	const onChange = ({ target: { value } }) => {
		console.log('Group cargo', value);
		setValue(value);
	};
	
	return (
		<>
			<Divider>
				 
			</Divider>
				<Form.Item
					style={{flexWrap: 'nowrap'}}
					name="groupType"
					label=" "
					>
					<div style={{flexWrap: 'nowrap'}} >
						<Radio.Group  options={options} onChange={onChange} value={value} >
						{
							options.map((radio, idx )=>{
								return (
									<Radio key={idx} value={radio.value}>radio.label</Radio>
								)
							})
						}
	
						</Radio.Group>
					</div>
				</Form.Item>
		</>
	)
}


export  default  GroupsCargo;