export  async  function fetchSettingsFormInstance(form)
{
	let params = new URLSearchParams(window.location.search);
	let instance_id = params.get('instance_id');
	let url = '/wp-json/dellin-shippment/v1/dellin-settings-plugin/'+instance_id;

	let options = {
		method: "POST",
		headers: {
			"X-WP-Nonce": dellinVars.wp_rest_nonce
		},
		body: JSON.stringify(form)
	}
	
	let request = await fetch(url, options);

	return await request.json();
}


