import {
	Button,
	message,
} from 'antd';
import {useContext} from 'react';
import { AppContext } from "../../contexts/FormContext";
import { fetchSettingsFormInstance} from '../../fetch';
import FieldsShipments from "../../stricts/FieldsShipment";
import fieldsAuth from "../../stricts/FieldsAuth";
import SenderFields from "../../stricts/SenderFields";
import CargoParams from "../../stricts/FieldsCargoParams";




const SavedButton = () =>
{
	const context = useContext(AppContext);
	const form = context.form;

	const [messageApi, contextHolder] = message.useMessage();


	const getFormValues = () => {
		let appForm = new FormData();

		fieldsAuth.map((field) => {
			appForm.append(
				field.name,
				form.getFieldValue(field.name) ? form.getFieldValue(field.name) : "",
				);
		});


		FieldsShipments.map((field) => {
			appForm.append(
				field.name,
				form.getFieldValue(field.name) ? form.getFieldValue(field.name) : "",
				);
		});

		SenderFields.map((field) => {
			appForm.append(
				field.name,
				form.getFieldValue(field.name) ? form.getFieldValue(field.name) : "",
				);
		});

		CargoParams.map((field) => {
			appForm.append(
				field.name,
				form.getFieldValue(field.name) ? form.getFieldValue(field.name) : "",
				);
		});

		appForm.append(
			"additionalPackages",
			form.getFieldValue("additionalPackages")
				? form.getFieldValue("additionalPackages")
				: "",
				);
		appForm.append(
			"isLogs",
			form.getFieldValue("isLogs") ? form.getFieldValue("isLogs") : "",
			);

		appForm.append('intervalWork', form.getFieldValue('intervalWork')?
			[form.getFieldValue('intervalWork')[0].format("HH:mm"),form.getFieldValue('intervalWork')[1].format('HH:mm')]:"");

		appForm.append('intervalLunch', form.getFieldValue('intervalLunch')?
			[form.getFieldValue('intervalLunch')[0].format("HH:mm"),form.getFieldValue('intervalLunch')[1].format('HH:mm')]:"");

		appForm.append('derrivalType', (form.getFieldValue('derrivalType')));


		let json = {};

		appForm.forEach(function (value, key) {
			json[key] = value;
		});

		return json;
	};

	const key = 'updatable';
	const onSumbitFormValues = async  (e) => {
		messageApi.open({
			key,
			type: 'loading',
			content: ' ..',
			duration: 2.5,
			className: 'custom-class',
			style: {
				marginTop: '10vh',
			},
		});
		e.preventDefault();

		console.log("Button Event", e);
		console.log(getFormValues());

		let response = await fetchSettingsFormInstance(getFormValues());

		console.log(response.state);
	
		if(response.state != 'update' || response.state != 'success' ){
				messageApi.open({
					key,
					type: 'error',
					content: '! -   .    ',
					duration: 2.5,
					className: 'custom-class',
					style: {
						marginTop: '10vh',
					},
			});
		}
		
		if(response.state == 'success')
		{
			messageApi.open({
				key,
				type: 'success',
				content: response.message,
				duration: 2.5,
				className: 'custom-class',
				style: {
					marginTop: '10vh',
				},
			});
		}
		
		if(response.state == 'update')
		{

			messageApi.open({
				key,
				type: 'warning',
				content: response.message,
				duration: 2.5,
				className: 'custom-class',
				style: {
					marginTop: '10vh',
				},
			});
		}

		setTimeout(messageApi.destroy, 5000);

	}
	return (
			<>
			{contextHolder}
			<Button onClick={onSumbitFormValues}
				htmlType="submit"
				type="primary" >
				 
			</Button>
			</>
		);
}

export default SavedButton;
