import {useState} from 'react';
import {
	Form,
	Divider,
	Flex,
	Switch
} from "antd";

const Insurance = () => {
	
	const [isInsurance, setIsInsurance] = useState(false);

	const onInsurance = (data) => {
		if(!data){
			setIsInsurance(true);
		}

		if(data){
			setIsInsurance(false);
		}
		console.log(isInsurance);
		console.log(data);
	}
	
	
  return(
	  <>
	  <Divider>
		   
	  </Divider>
	  <Flex gap="middle" vertical>
		  <Form.Item 
			  name="isInsurance"
			  label="  "
			  tooltip="    ,      "
			  valuePropName="checked">

			  <Switch 
				  onChange={onInsurance}
			  />

		  </Form.Item>
	  </Flex>
	  </>
  )
}

export  default  Insurance;