import {
    useState,
    useContext,
    useEffect,
} from 'react';
import { 
    Form,
    Input,
    Select,
    Alert,
	notification
} from 'antd';
import {fetchGetTerminals} from '../../fetch';
import { AppContext } from '../../contexts/FormContext';


const TerminalOrAddressField = ({type, kladr}) => 
{

    const context = useContext(AppContext);

    const [terminalsList, setTerminalsList] = useState([]);
    const [response, setResponse] = useState({});
	const [isLoading, setIsLoading] = useState(false);

	const [api, contextHolder] = notification.useNotification();



    useEffect(()=>{

        if(context.isKeyValid){
            if(kladr){
                const appkey = context.appkey;
					setIsLoading(true)
					 fetchGetTerminals(appkey, kladr).then((result)=>{
					console.log('getTerminalBody:',result);
					if(result.status == 'success')
					{
						setResponse(result);
						context.form.setFieldValue('fieldTerminal', '');
						setIsLoading(false);
					}

					if(result.status == 'error')
					{
						api.warning({
							message: ' ',
							description: result.message,
						})
						setIsLoading(false);
					}
				}).catch(console.error);
            }
        }
        
    }, [kladr]);


    useEffect(()=>{
		if(response.terminals)
		{
			context.dataTerminals = response.terminals;
			console.log(response);
			console.log('context DataTerminals, ', context);
		}

       
    },[response]);

    useEffect(()=>{
        console.log('context terminals, ',context);
        if(context.dataTerminals)
        {

            let result = [];

            const terminals = context.dataTerminals;
            
            console.log('Terminals ', terminals);

            if(terminals){
				result.push({label:" ", value: ''})
				terminals.map((terminal)=>{
					result.push({label: terminal.label, value:terminal.id})
				})

                setTerminalsList(result);  
            }
        }
    }, [response, context]);

    const onSelect = (value) => {
        
    }

	console.log('derrivalType, ', type);

    return (
        <>
		{contextHolder}

        {!type && (
            <Form.Item
            name="fieldAddress"
            label=" "
            tooltip=":       : , ,  , ,  .
            : ,  , , . , .1">
                
            <Input />

        </Form.Item>) 
        }
        {type && (
            context.isKeyValid ?(
            <Form.Item 
            name="fieldTerminal"
            label="">
                <Select 
                    loading={isLoading}
                    options={terminalsList}
                />
            </Form.Item>
            ) : (
                    <Alert
                    message="!"
                    description="   .     ."
                    type="warning"
                    showIcon
                    closable
                  />)
            )
        }
        </>
    )

}


export default TerminalOrAddressField;