import {useState, useEffect, useContext} from 'react';

import { AppContext } from '../contexts/FormContext';
import {
	fetchGetOpf,
	fetchGetCounteragents,
	fetchTerminalList, fetchGetTerminals
} from '../fetch';



const useAuthFields = () => {

    const context = useContext(AppContext);

    const [fieldAuth, setFieldsAuth] = useState({
        appkey: '',
        login: '',
        password: ''
    
      });

    const [isLoading, setIsLoading] = useState(true);
    const [responses, setResponses] = useState([]);
	const [optionsCounteragent, setOptionsCounteragent] = useState([]);//fix problem withCounteragents field

    
      const update = (prev, name, value) => {
          return {
            ...prev,
            [name]: value
          }
      }
    
      const onBlurHandler = (event) => {
		  if(event){
			  const value = event.target.value;
			  const id = event.target.id;
			  setFieldsAuth((prev)=>(update(prev, id, value)));
		  }

      }

	const onLoadData = () =>
	{
		  if(dellinVars.options)
		  {
			  let appkey = dellinVars.options.appkey;
			  let login = dellinVars.options.login;
			  let password = dellinVars.options.password;
			  let stateAuth = {};
			  if(appkey && appkey != '' )
			  {
				  stateAuth.appkey = appkey;
				  if(login && password &&
				     login != '' && password != '')
				  {
					  stateAuth.login = login;
					  stateAuth.password = password;
				  }

				  setFieldsAuth(stateAuth);
			  }

		  }

		  if(!dellinVars.options.appkey)
		  {
			  setIsLoading(false);
		  }
	  
	  
	}

      

      useEffect( ()=>{
		  
		  let queries = [];
		  let kladr = dellinVars.options.locationKladr;
		  
        if(
            fieldAuth.appkey != ''
        ){
			queries.push(fetchGetOpf(fieldAuth.appkey));

			if(kladr && kladr != '')
			{
				console.log(kladr);
				queries.push(fetchGetTerminals(fieldAuth.appkey, kladr));
			}

		  if(
           fieldAuth.login != '' &&
           fieldAuth.password != '')
		  {
			  queries.push(fetchGetCounteragents(fieldAuth.appkey,
												 fieldAuth.login,
												 fieldAuth.password));
		  }
		  if(queries.length > 0)
		  {
			Promise.all(queries)
								.then((values)=>{
									setResponses([...values]);
								})
		  }

		}

    }, [fieldAuth])

      useEffect(()=>{
        console.log('responses ', responses)
		if(responses.length == 0){
			console.log('  ');
			onLoadData();
			return;
		}
        if(!responses || responses == [])
        {
          return;
        }
		console.log(' ');
        
        if(Array.isArray(responses)){
          responses.forEach((response,idx)=> {
            console.log('resp '+idx+' ', response);
            if(response.code == '200')
            {

			  if(response.countries)
			  {
				  context.isKeyValid = true;
				  context.appkey = fieldAuth.appkey;
				  context.dataCountries = response.countries;
			  }

			  if(response.opf)
			  {
				  context.dataOpf = response.opf;
			  }


			  if(response.counteragents)
			  {
				  if(!response.sessionID.errors){

					  context.sessionID = response.sessionID;
					  context.isLoginProfile = true;

					//  if(response.counteragents.length > 0){
						  console.log('Response counteragents', response.counteragents);
						  let result = [];
						  Object.values(response.counteragents).forEach((item)=>{
							const val = {label: item.name, value: item.uid};
	   							result.push(val);
						  });
						  context.dataCounteragents = result;

						 console.log('Setted counteragents',result);

	//				  }

				  } else {
					  context.isLoginProfile = false;
					  context.dataCounteragents = [];
				  }


			  }

				if(response.terminals)
				{
					console.log('response terminals', response.terminals)
					context.dataTerminals = response.terminals;
			  	}
				setIsLoading(false);
            }
          })
      }


        console.log(context);
		if(!context.appkey)
		{
			setIsLoading(false);
		}
        console.log(responses);


      }, [responses])
    
      //TODO   - state managment;
      //   :
      //     ,         
      //    -   ,     . 
      //        ,      .
      //       .
	  //          dellinVars;

    return [onBlurHandler, onLoadData, setFieldsAuth, fieldAuth, isLoading, responses,
			optionsCounteragent];

}

export default useAuthFields;
