import { 
    Divider,
    Flex,
    Select,
    Input,
    Switch,
    Form,
    Spin
} from "antd";

import {
    useState,
    useContext,
    useEffect
} from "react";

import { AppContext } from "../../contexts/FormContext";

import { fetchKLADRCode } from "../../fetch";
import TerminalOrAddressField from "./TerminalOrAddressField";



const CargoDerrivalBlock = ({derrivalType, setDerrivalType}) => {


    const context = useContext(AppContext);
    
    const [value, setValue] = useState('');
    const [options, setOptions] = useState([]);
    const [data, setData] = useState({});
    const [fetchingKLADR, setFetchingKLADR] = useState(false)

	//       .

	console.log(context.form.getFieldValue('derrivalType'))

    const onDerrivalTypeChange = (data) => {

        if(!data){
            
            setDerrivalType(false);
            context.derrivalType = derrivalType;
        }
        
        if(data){
            
            setDerrivalType(true);
            context.derrivalType = derrivalType;
        }


        console.log(derrivalType);
        console.log(data);
    }

    const getKLADROptions = (query) => {
        
        if(context.isKeyValid)
        {
            setFetchingKLADR(true);
            fetchKLADRCode(context.appkey, query).then((result)=>{
                setData(result);
                console.log(options);
            });
        }

    }

    useEffect(()=>{

        console.log('Data , ', data);

        if(data.code == 200 ){
            let rows = [];

            if(data.result.error)
            {
                setOptions(null);
                setFetchingKLADR(false);
                return;
            }
            
            if(!data.result.map)
            {
                setOptions(null);
                setFetchingKLADR(false);
                return;
            }

            data.result.map((item)=>{
                const row = {label: item.label, value: item.code};
                rows.push(row);
            });
    
            setOptions(rows);
            setFetchingKLADR(false);

        }
        
        
    }, [data]);

    const onSelect = (value) => {
       console.log(", ", value);

       setValue(value);
       context.form.setFieldValue('locationKladr', value);
	   
	   if(options.length > 0)
	   {
		   options.map((city)=>{
			   if(city.value == value)
			   {
				   context.form.setFieldValue('location', city.label);
			   }
		   })
	   }
       console.log(value);
    };


    const onSearch = (value) => {
        console.log(value);
        getKLADROptions(value);
    };

    /**
     * :
     * 1.             : 
     * "          ".
     * 2. 
     */

    return (
    <>
        <Divider>
             
        </Divider>
        <Flex gap="middle" vertical>
            <Form.Item
                name="location"
                label=' '>
                
                <Select
                showSearch
                filterOption={false}
                options={options}
                style={{
                    width: 400,
                }}
                onSearch={onSearch}
                onChange={onSelect}
                placeholder="   "
                notFoundContent={fetchingKLADR ? <Spin size="small" /> : null}
                />

            </Form.Item>


            <Form.Item 
                name="locationKladr"
                label="">
                
                <Input 
                value={value} 
                disabled/>
                
            </Form.Item>

            <Form.Item 
                name="derrivalType"
                label=" "
                valuePropName="checked">
                
                <Switch 
                    checkedChildren=" "
                    unCheckedChildren=" "
                    onChange={onDerrivalTypeChange}
                    value={derrivalType}
                    defaultChecked />

            </Form.Item>


            <TerminalOrAddressField type={derrivalType}
                                    kladr={value}
                                     />
        </Flex>
    </>
    )
}


export default CargoDerrivalBlock;