import {Flex, Form, Input } from 'antd';
import { useEffect } from 'react';
import {fetchGetOpf, fetchGetCounteragents} from '../../fetch';
import useAuthFields from '../../hooks/useAuthFields';
import fieldsAuth from '../../stricts/FieldsAuth';


const AuthFields = () => {

  const fields = fieldsAuth;

  const [onBlurHandler, fieldAuth] = useAuthFields();


  return ( 
    <Flex gap="middle" vertical>
        {
        fields.map((field, idx) => {

          let rules = (field.rules)?field.rules:null;
          let tooltip = (field.tooltip)?field.tooltip:null;

          return (
            <Form.Item 
              key={idx}
              label={field.label}
              name={field.name}
              rules={rules}
              tooltip={tooltip}
              onBlur={onBlurHandler}
            >
              {field.name == 'password'?<Input.Password size="small" /> : <Input />}
            </Form.Item>
          )
        })
          
      }
    </Flex>
  )
}

export default AuthFields;