import {useState} from 'react';
import { Form, Divider, Flex, Switch } from "antd";


const ArrivalGoodsLoading = () => {


    const [arrivalGoodsLoading, setArrivalGoodsLoading] = useState('terminal');

    const onArrivalGoodsLoading = (data) => {

        if(!data){
            setArrivalGoodsLoading('address');
        }
        
        if(data){
            setArrivalGoodsLoading('terminal');
        }

        console.log(arrivalGoodsLoading);
        console.log(data);
    }

    return (
        <>
        <Divider>
              
        </Divider>
        <Flex gap="middle" vertical>
            <Form.Item 
                name="arrivalGoodsLoading"
                label=" "
                valuePropName="checked">
                
                <Switch 
                    checkedChildren=" "
                    unCheckedChildren=" "
                    onChange={onArrivalGoodsLoading}
                />

            </Form.Item>
        </Flex>
        </>
    )
}

export default ArrivalGoodsLoading;