import {
    Divider,
    Flex,
    Form,
    InputNumber,
    Switch
 } from 'antd';

import { 
    CheckOutlined,
    CloseOutlined
 } from '@ant-design/icons';

 import {
    useState
 } from 'react';

import  fieldsDefaultParams  from '../../stricts/FieldsDefaultCargoParams';


const DefaultCargoParams = () => {

  const fields =  fieldsDefaultParams;

  const [checked, setChecked] = useState(false);

  return ( 
    <>
        <Divider>
           
        </Divider>  
        <Flex gap="middle" vertical>
            <Form.Item
                label="    ?"
                name="isUseDefaultCargoParams"
                valuePropName="checked"
            >
                <Switch
                    checkedChildren={<CheckOutlined />}
                    unCheckedChildren={<CloseOutlined />}
                    onChange={(e)=>(setChecked((prev)=>(!prev)))}
                    value={checked}
                    />
            </Form.Item>
            {
            fields.map((field, idx) => {

            let tooltip = (field.tooltip)?field.tooltip:null;

            return (
                <Form.Item 
                key={idx}
                label={field.label}
                name={field.name}
                tooltip={tooltip}
                >
                <InputNumber 
                    formatter={(value)=> `${value}, ${field.unit}`}
                    min={50}
                    />
                </Form.Item>
            )
            })
            
        }
        </Flex>
    </>
  )
}

export default DefaultCargoParams;