import { 
    Divider,
    Flex,
    Form,
    Input,
    Select
 } from "antd";

import { 
    useState,
    useContext,
    useEffect
 } from 'react';

import { AppContext } from '../../contexts/FormContext';

const YuriData = () => {

    const context = useContext(AppContext);

    const [optionsCountries, setOptionsCounries] = useState([]);
    const [optionsOpf, setOptionsOpf] = useState([]);

    const [country, setCountry] = useState('');
    const [opf, setOpf] = useState('');

    const onChangeCountry = (value) => {

        setCountry(value);
        console.log(`selected ${value}`);
    };

    const onChangeOpf = (value) => {
        setOpf(value);
    }

    const onSearch = (value) => {
        console.log('search:', value);
    };

    

      // Filter `option.label` match the user type `input`
    const filterOption = (input, option) =>
    (option?.label ?? '').toLowerCase().includes(input.toLowerCase());

    useEffect(()=>{

        if(!context.dataCountries)
        {
            console.log('!context.dataCountries ',context.dataCountries)
            setOptionsCounries(null);
            return;
        }


        if(context.dataCountries){

            
            let result = [];

            Object.keys(context.dataCountries).forEach((item)=>{
                
                
                const val = {label: context.dataCountries[item], value: item};

                result.push(val);
                
            });
    
            console.log(result);
            
            setOptionsCounries(result);
        }

		if(context.dataOpf)
		{
			let form = context.form;
			let valueCountry = form.getFieldValue("country");
			let valueOPF = form.getFieldValue("opf");

			console.log('find country: ', valueCountry);
			if(valueCountry && valueCountry!='')
			{
				setCountry(valueCountry);

			}


		}



    }, [context]);



    useEffect(()=>{


        if(!context.dataOpf)
        {
            console.log('!context.dataOpf ', context.dataOpf)
            setOptionsCounries(null);
            return;
        }

        if(context.dataOpf)
        {
            if(country == '')
            {
                
                setOptionsOpf(null);
				console.log(' ')
                return;
            }

           const storage = context.dataOpf[country];


           let result = [];
              Object.keys(storage).forEach((item)=>{

                  const val = {label: storage[item], value: item};
                  result.push(val);

              });

            console.log(result);
            setOptionsOpf(result);

        }

    }, [country]);
   

    return (        
        <>
            <Divider>
                   
            </Divider>
           
            <Flex gap="middle" vertical>
                <Form.Item 
                        name="country"
                        label=""
                        >
                    <Select
                        showSearch
                        placeholder="       "
                        optionFilterProp="children"
                        onChange={onChangeCountry}
                        onSearch={onSearch}
                        filterOption={filterOption}
                        options={optionsCountries}
                        />
                </Form.Item>
                <Form.Item 
                        name="opf"
                        label=""
                        >
                            
                    <Select
                        showSearch
                        placeholder="      "
                        optionFilterProp="children"
                        onChange={onChangeOpf}
                        onSearch={onSearch}
                        filterOption={filterOption}
                        options={optionsOpf}
                        />
                </Form.Item>
                <Form.Item
                    name="yuriAddress"
                    label=" "
                    tooltip=":      : , ,  , ,  .
                    : ,  , , . , .1"
                    >
                        <Input />

                </Form.Item>
                <Form.Item
                    name="inn"
                    label=""
                    >
                        <Input />

                </Form.Item>
				<Form.Item
					name="companyName"
					label=" "
					>
					<Input />

				</Form.Item>
                    

                
            </Flex>
        </>
    );

}

export default YuriData;