import {useState} from 'react';
import {Form, Divider, Flex, Switch } from "antd";


const SmallCargo = () => {


    const [smallCargo, setSmallCargo] = useState(false);

    const onSmallCargo = (data) => {
        if(!data){
            setSmallCargo(true);
        }
        
        if(data){
            setSmallCargo(false);
        }
        console.log(smallCargo);
        console.log(data);
    }

    return (
        <>
        <Divider>
          
        </Divider>
        <Flex gap="middle" vertical>
            <Form.Item 
                name="includeSmallCargo"
                label="  ' '"
                valuePropName="checked">
                
                <Switch 
                    onChange={onSmallCargo}
                />

            </Form.Item>
        </Flex>
        </>
    )
}

export default SmallCargo;