import {
  Flex,
  Alert
} from 'antd';
import CargoDerrivalBlock from './CargoDerivalBlock';
import TimeWorkWarehouse from './TimeWorkWarehouse';
import BServicesToShipmentsAddress from './BServicesToShipmentsAddress';
import DeferredShipment from './DeferredShipment';
import {
	useState,
	useContext
} from 'react';
import { AppContext } from "../../contexts/FormContext";


const ShipmentsFields = () => {

	const context = useContext(AppContext);
	//        .
	const initialValue = context.form.getFieldValue('derrivalType');


  	const [derrivalType, setDerrivalType] = useState(initialValue);





    return (
      <Flex gap="middle" vertical>
        <CargoDerrivalBlock derrivalType={derrivalType}
                            setDerrivalType={setDerrivalType}/>
        {(!derrivalType)?(
        <>
          <TimeWorkWarehouse />
          <BServicesToShipmentsAddress />
        </>
        ):(
          <Alert
          message=" "
          description="       << >>"
          type="info"
          showIcon
        />
        )
        }

        <DeferredShipment />
      </Flex>
    )
}



export default ShipmentsFields;