import {useState, useEffect, useContext} from 'react';

import { AppContext } from '../contexts/FormContext';

const useCounteragents = () => {
	
	let context = useContext(AppContext);
		
	const [options, setOptions] = useState([]);
	
	useEffect(()=>{
		
		if(context.dataCounteragents)
		{
			setOptions(context.dataCounteragents);
		}
		
	}, [context]);
	
	const handleClick = () => {
		if(context.dataCounteragents)
		{
			setOptions(context.dataCounteragents);
		}
	}
		
	return [options, handleClick];
}


export default useCounteragents;
